import Vue from 'vue'
import Vuex from 'vuex'

import modules from './modules'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    host: '',
  },
  mutations: {
    SET_HOST(state, value) {
      state.host = value
    },
  },
  getters: {
    host: state => state.host
  },
  modules,
})
